.lead {
  max-width: 1343px;
  min-height: 448px;
  display: flex;
  align-items: center;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(../../image/img-main.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border: 2px solid #2B2B2B;
  box-shadow: 0px 7px 16px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
}

.lead__title {
  font-family: 'Philosopher';
  font-weight: 700;
  font-size: 65px;
  line-height: 102.5%;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.4);
  margin: 0;
  max-width: 300px;
  padding-left: 10%
}

.lead__description {}

@media screen and (max-width: 1343px) {
  .lead {
    min-height: 400px;
  }

  .lead__title {
    font-size: 55px;
  }
}

@media screen and (max-width: 1023px) {
  .lead {
    min-height: 350px;
  }

  .lead__title {
    font-size: 38px;
    max-width: 200px;
  }
}

@media screen and (max-width: 767px) {
  .lead {
    min-height: 210px;
  }

  .lead__title {
    font-size: 25px;
    max-width: 100px;
  }
}
